				<p>Samhllet som vxte upp kring bruket var till stor del sjlvfrsrjande, med ett betydande jordbruk och egen lanthandel. I centrum lg herrgrden tillsammans med kyrkan, skolan, jordbruksbyggnader och tjnstemannabostder. Utsprida lngs bruksgatan, mot masugnen och hamrarna, lg brukstorp fr hytte- hammar- och dagsverksarbetare.<br />r 1803 fanns vid Lgd bruk 51 stycken bostder byggda fr brukets arbetare. Vid Lgd fanns inga arbetslngor, som var mycket vanligt vid andra bruk. I stllet bodde en stor del av de anstllda i torp ute p brukets gor.</p>
