import { Link } from "react-router-dom";

import AnimateFadePage from '../includes/_AnimateFadePage';
import { Swiper, SwiperSlide } from 'swiper/react'
import { EffectFade, Autoplay, Navigation } from 'swiper';

import img1 from './../assets/images/q1.jpg'
import img3 from './../assets/images/q.jpg'
import placeholder from './../assets/images/placeholder-icon.jpg'

const slider = [img1, img3]

const Home = () => {
	return (
		<AnimateFadePage>
			<section className='hide'>
				<h2>Vlkommen!</h2>
			</section>

			<section className='full-width-small'>
				<Swiper
					modules={[Navigation, EffectFade, Autoplay]}
					effect="fade"
					autoplay={{
						delay: 4000,
						disableOnInteraction: false,
						pauseOnMouseEnter: false
					}}
					loop={true}
					navigation={true}
				>
					{slider.map(
						(img, index) =>
							<SwiperSlide key={index}><img className='swiper-image' src={img} alt='img1' /></SwiperSlide>
					)}
				</Swiper>
			</section>

			<section>
				<div className='home-brands'>
					<div>
						<img className='brand' src={placeholder} alt='logo' />
						<h3>Bed and Brekfast</h3>
						<p>Kom och upplev hergrdslivet hr hos oss i Lgd utanfr Timr.</p>
						<br />
						<Link to='/BaB' className="btn">Ls mer..</Link>
					</div>
					<div>
						<img className='brand' src={placeholder} alt='logo' />
						<h3>Kaf</h3>
						<p>Besk vrat helgkaf dr du kan sitta i en helt unik milj.</p>
						<br />
						<Link to='/kafe' className="btn">Ls mer..</Link>
					</div>
					<div>
						<img className='brand' src={placeholder} alt='logo' />
						<h3>Keramik</h3>
						<p>Frn kurser till hantverk i bde keramik och trsniderier.</p>
						<br />
						<Link to='/keramik' className="btn">Ls mer..</Link>
					</div>
				</div>
				<div className='home-brands'>
					<div>
						<img className='brand' src={placeholder} alt='logo' />
						<h3>I nrheten</h3>
						<p>Lorem ipsum dolor</p>
						<br />
						<Link to='/aktiviteter' className="btn">Ls mer..</Link>
					</div>
					<div>
						<img className='brand' src={placeholder} alt='logo' />
						<h3>Galleri</h3>
						<p>Lorem ipsum dolor</p>
						<br />
						<Link to='/galleri' className="btn">Ls mer..</Link>
					</div>
					<div>
						<img className='brand' src={placeholder} alt='logo' />
						<h3>Historia</h3>
						<p>Lorem ipsum dolor</p>
						<br />
						<Link to='/historia' className="btn">Ls mer..</Link>
					</div>
				</div>
			</section>

			<section className="hide">
				<p>Bjrn Schumacher och Mattias Rustan ger Lgd Herrgrd. Byggnaden fungerar som privat boende, Bed and Brekfast och festlokal ret om. P sommaren har de ven cafe dr du kan sitta i en unik milj.</p>
				<p>P Herrgrden finns 8 sngplatser som r frdelade med 1 enkelrum 2 dubbelrum och 1 trebddsrum. Ca 60 sittplatser i frdelat p tv rum. Herrgrdens stolta snickartraditioner finns nnu bevarade och skapar en vlkomnande atmosfr. De ljus och luftiga salongerna r stilfullt mblerade. Kristallkronorna och de vackra kakelugnarna frstrker den genuina miljn. Rummen r inreda i gammal stil och frstrka den miljn som det en gng har varit.</p>
				<p>Anlggningen uthyres till enskilda svl som freningar eller fretag. Hr hls ven helgkurser i olika konst och hantverkskurser.</p>
			</section>

			<footer>
				<div className='footer hide-off'>
					<div>

						<div>
							<h3 className='widget-title'>Om oss</h3>
							<p>Bjrn Schumacher och Mattias Rustan ger Herrgrden. Byggnaderna fungerar som privat boende, Bed and Brekfast och festlokal ret om. P sommaren har vi ven cafe dr du kan sitta i en unik milj.</p>
							<p>P Herrgrden finns det tta sngplatser som r frdelade med ett enkelrum tv dubbelrum och ett trebddsrum. Ca 60 sittplatser i frdelat p tv rum. Herrgrdens stolta snickartraditioner finns nnu bevarade och skapar en vlkomnande atmosfr. De ljus och luftiga salongerna r stilfullt mblerade. Kristallkronorna och de vackra kakelugnarna frstrker den genuina miljn. Rummen r inreda i gammal stil och frstrka den miljn som det en gng har varit.</p>
						</div>

						<div>
							<h3 className='widget-title'>vrigt</h3>
							<ul>
								<li><Link to='/historia'>Historia</Link></li>
								<li><Link to='/aktiviteter'>Omrdet</Link></li>
								<li><Link to='/galleri'>Galleri</Link></li>
							</ul>
							<h3 className='widget-title'>Adress</h3>
							<ul>
								<li>Bruksvgen 27</li>
								<li>861 92 Bergeforsen</li>
								<li>072-230 33 37</li>
								<li>logdoherrgard@icloud.com</li>
							</ul>
							<a href='https://www.google.com/maps/place/L%C3%B6gd%C3%B6+Bruk/@62.557407,17.383419,12z/data=!4m5!3m4!1s0x0:0x6bc2d023e0afb519!8m2!3d62.5576457!4d17.3841999?hl=sv' target='_blank' rel="noreferrer" className='btn'>Google Maps</a>
						</div>

					</div>
				</div>
			</footer>
		</AnimateFadePage>
	);
}

export default Home